<template>
<div style="margin-left:5%;  text-align:left;">
    <div>--------------------------------마인드 만들기-------------------------------------------</div>
<div ><br><br><br><br>

    <form v-on:submit.prevent="makeMind"> 
    <div >
      <label for="title">마인드 이름(title):</label>
      <input id="title" type="text" v-model="title" />
    </div>
     <div>
      <label for="subject">주제:</label>
      <input id="subject" type="text" v-model="subject" />
    </div>
    <div>
      <label for="hashtag">태그 이름(태그 한개당 앞에#필수입력):</label>
      <input id="hashtag" type="text" v-model="hashtag"  />
    </div>
      <div>
      <label for="explanation">설명:</label>
      <input id="explanation" type="text" v-model="explanation" />
    </div>


    <button type="submit">생성하기</button>

  </form>
  


</div>

<div>------------------------------- 마인드 조회 --------------------------------------------</div>
<div>
 <button
        @click="mindRead"
    >마인드 읽어오기</button></div>
<div>
 <table class="table" style="text-align: center; margin-left: auto; margin-right: auto;">
        <colgroup>
          <col width="20%" />
          <col width="20%" />
          <col width="20%" />
          <col width="20%" />
          <col width="20%" />
        </colgroup>
        <tr class="bg-primary">
          <td>관리자</td>
          <td>제목</td>
          <td>해시태그</td>
          <td>주제</td>
          <td>설명</td>
        </tr>
        <tr v-for="temp in datas" v-bind:key="temp" >
          <td>{{temp.admin}}</td>
          <td>{{temp.title}}</td>
          <td>{{temp.hashtag}}</td>
          <td>{{temp.subject}}</td>
          <td>{{temp.explanation}}</td>
        </tr>
      </table>
  </div>

