<div>------------------------------- 마인드 수정 by ID--------------------------------------------</div>
마인드 아이디는 (페이지안으로 들어가면 마인드 아이디를 서버에서 프론트로 보내줌)
   <form v-on:submit.prevent="searchByID">
    <div>
      <label for="up_id">일단 ID로 조회하기(입력):</label>
      <input id="up_id" type="text" v-model="up_id" />  <button type="submit">수정할 마인드 id 조회</button>
    </div>

  </form>

    <form v-on:submit.prevent="updateMind">
    <div>
      <label for="up_title">마인드 이름(title):</label>
      <input id="up_title" type="text" v-model="up_title" />
    </div>
     <div>
      <label for="up_subject">주제:</label>
      <input id="up_subject" type="text" v-model="up_subject" />
    </div>
    <div>
      <label for="up_hashtag">태그 이름(태그 한개당 앞에#필수입력):</label>
      <input id="up_hashtag" type="text" v-model="up_hashtag"  />
    </div>
      <div>
      <label for="up_explanation">설명:</label>
      <input id="up_explanation" type="text" v-model="up_explanation" />
    </div>



    <button type="submit">수정하기</button>

  </form>
   

<div>------------------------------- 마인드 삭제 --------------------------------------------</div>
(나중에 프론트에서 이동할때, created로 mind에 대한id값을 불러와야함)
<form v-on:submit.prevent="deleteByID">
    <div>
      <label for="up_id">ID로 삭제하기:</label>
      <input id="up_id" type="text" v-model="up_id" />  <button type="submit">(권한있으면)해당 id 삭제</button>
    </div>

  </form>

</div>
</template>

<script>

import axios from 'axios';
 
export default {
  data:function(){
    return{
       id : '',
       pass : '',
       title: '',
       hashtag:'',
       subject:'',
       explanation:'',
       up_id : '',
       up_pass : '',
       up_title: '',
       up_hashtag:'',
       up_subject:'',
       up_explanation:'',
       datas: [],
       dataByID:{},
    }
  },
  methods:{
   
     makeMind:function(){
      let form = new FormData()
      form.append('title', this.title) 
      form.append('hashtag',this.hashtag)
      form.append('subject', this.subject) 
      form.append('explanation', this.explanation) 
      axios.post(`http://localhost:8000/mindwiki/mind`,form).then(({ data }) => {
      
      alert(data.message);
      
    });
    
    },
       mindRead(){

       axios.get(`http://localhost:8000/mindwiki/mind/read`).then(({ data }) => {
      this.datas=data;
      alert("로그인 되어있다면 모든 데이터를 조회합니다.");
      
    });
      
    },  mindReadByID(){

       axios.get(`http://localhost:8000/mindwiki/mind/read/2`).then(({ data }) => {
      this.dataByID=data;
      alert("MindID 2번데이터만 조회합니다.(front에서 수정필요)");
      
    });
      
    },
      searchByID(){

      axios.get(`http://localhost:8000/mindwiki/mind/read/`+this.up_id).then(({ data }) => {
      
      this.up_title=data.title;
      this.up_subject=data.subject;
      this.up_explanation=data.explanation;
      this.up_hashtag=data.hashtag;
      // console.log(data);
      alert(data.mindID+"번호 데이터를 조회했습니다.");
      
    });
      
    },
      deleteByID(){

      axios.delete(`http://localhost:8000/mindwiki/mind/delete/`+this.up_id).then(({ data }) => {
      
    
      // console.log(data);
      alert(data.message);
      
    });
      
    },

     updateMind:function(){
      let form = new FormData()
      form.append('MindID', this.up_id)
      form.append('title', this.up_title) 
      form.append('hashtag',this.up_hashtag)
      form.append('subject', this.up_subject) 
      form.append('explanation', this.up_explanation) 
      axios.put(`http://localhost:8000/mindwiki/mind/update`,form).then(({ data }) => {
      
      alert(data.message);
      
    });
    
    },

  }
};
</script>

