<template>
    <div>
   <form v-on:submit.prevent="submitForm">
    <div>
      <label for="id">Email:</label>
      <input id="id" type="text" v-model="id" />
    </div>
    <div>
      <label for="pass">PW:</label>
      <input id="pass" type="password" v-model="pass"  />
    </div>
    <button type="submit">login</button>
  </form>
  
  <div>
 <img
        class="kakao_btn"
        src="@/assets/kakao.png"
        @click="loginWithKakao"
    />
  </div>

    <div>
       <a :href="'https:'+'//accounts.google.com/o/oauth2/v2/auth?'+
        'scope=https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/userinfo.email&'+
        'access_type=online&'+
        'include_granted_scopes=true&'+
        'response_type=code&'+
        'redirect_uri=http%3A//localhost:8000/mindwiki/GoogleOAuth&'+
        'client_id=659791765906-faeludmkkn7l8vqlk37pqlhhisu4n1hb.apps.googleusercontent.com'
 
       ">
            googleLogin
      </a>
    </div>



<div>-------------------------------- 세션 확인 -------------------------------------------</div>
    <div>
    <button
        @click="sessionCheck"
    >세션확인</button></div>

<br>
<br>

    <div>
      <button
        @click="logout"
    >로그아웃</button></div>


    </div>


    
</template>



<script>

import axios from 'axios';
 
export default {
  data:function(){
    return{
       id : '',
       pass : '',
       title: '',
       hashtag:'',
       subject:'',
       explanation:'',
       up_id : '',
       up_pass : '',
       up_title: '',
       up_hashtag:'',
       up_subject:'',
       up_explanation:'',
       datas: [],
       dataByID:{},
    }
  },
  name: "LoginKakao",
  methods:{
      loginWithKakao() {
        const params = {
            redirectUri: "http://localhost:8000/mindwiki/oauth",
        };
        window.Kakao.Auth.authorize(params);
     },

    
    submitForm:function(){
      let form = new FormData()
      form.append('id', this.id) 
      form.append('pass',this.pass)
      

      axios.post(`http://localhost:8000/mindwiki/login`,form).then(({ data }) => {
      
      alert(data.message);
      
    });
    
    },
     makeMind:function(){
      let form = new FormData()
      form.append('title', this.title) 
      form.append('hashtag',this.hashtag)
      form.append('subject', this.subject) 
      form.append('explanation', this.explanation) 
      axios.post(`http://localhost:8000/mindwiki/mind`,form).then(({ data }) => {
      
      alert(data.message);
      
    });
    
    },
       sessionCheck() {
        axios.post(`http://localhost:8000/mindwiki/sessionCheck`).then(({ data }) => {
      
      alert(data);
      
    });

     },
       mindRead(){

       axios.get(`http://localhost:8000/mindwiki/mind/read`).then(({ data }) => {
      this.datas=data;
      alert("로그인 되어있다면 모든 데이터를 조회합니다.");
      
    });
      
    },  mindReadByID(){

       axios.get(`http://localhost:8000/mindwiki/mind/read/2`).then(({ data }) => {
      this.dataByID=data;
      alert("MindID 2번데이터만 조회합니다.(front에서 수정필요)");
      
    });
      
    },
      searchByID(){

      axios.get(`http://localhost:8000/mindwiki/mind/read/`+this.up_id).then(({ data }) => {
      
      this.up_title=data.title;
      this.up_subject=data.subject;
      this.up_explanation=data.explanation;
      this.up_hashtag=data.hashtag;
      // console.log(data);
      alert(data.mindID+"번호 데이터를 조회했습니다.");
      
    });
      
    },

     updateMind:function(){
      let form = new FormData()
      form.append('MindID', this.up_id)
      form.append('title', this.up_title) 
      form.append('hashtag',this.up_hashtag)
      form.append('subject', this.up_subject) 
      form.append('explanation', this.up_explanation) 
      axios.put(`http://localhost:8000/mindwiki/mind/update`,form).then(({ data }) => {
      
      alert(data.message);
      
    });
    
    },



     logout() {
      axios.post(`http://localhost:8000/mindwiki/mind/logout`)

      alert("로그아웃되었습니다.");
    }


  }
};
</script>